import { useEffect, useState } from "react";
import { Button, Input, Checkbox, Link } from "@heroui/react";
import { FaRegEye, FaRegEyeSlash } from "react-icons/fa6";
import { GetServerSideProps } from "next";
import Image from "next/image";
import { redirect } from "next/navigation";
import axiosInstance from "@/services";
import { useTranslation } from "react-i18next";
import { signIn, useSession } from "next-auth/react";
import { useRouter } from "next/router";
import toast from "react-hot-toast";




export default function Component() {
  const { t } = useTranslation();
  const { status } = useSession();
  const router = useRouter();
  const [loading, setLoading] = useState(false);
  const [isVisible, setIsVisible] = useState(false);
  const [isConfirmVisible, setIsConfirmVisible] = useState(false);
  const [error, setErrors] = useState<Record<string, string>>({});

  const toggleVisibility = () => setIsVisible(!isVisible);
  const toggleConfirmVisibility = () => setIsConfirmVisible(!isConfirmVisible);

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const data = Object.fromEntries(new FormData(event.currentTarget));

    if (data.email != data.confirmEmail) {
      setErrors({
        confirmEmail: "errors_confirm_email",
      });
      return;
    }

    if (data.password != data.confirmPassword) {
      setErrors({
        confirmPassword: "errors_confirm_password",
      });
      return;
    }

    setErrors({});
    setLoading(true);

    try {
      await axiosInstance.post("auth/register", {
        name: data?.name,
        email: data?.email,
        password: data?.password,
        sponsor_id: props.userid,
        side: props.position,
      });

      if (status != "authenticated") {
        signIn("credentials", {
          username: data.email,
          password: data.password,
          redirect: false,
        }).then(() => {
          router.replace("/");
        });
      } else {
        setLoading(false);
        toast.success("Usuario registrado correctamente");
        router.replace("/");
      }
    } catch (err: any) {
      if (err.response?.data?.message == "USER_ALREADY_EXISTS") {
        setErrors({
          confirmEmail: "errors_user_exists",
        });
      } else {
        setErrors({
          network: "error",
        });
      }
      setLoading(false);
    }
  };

  return (
    <div className="flex h-full w-full items-center justify-center min-h-screen">
      <div className="flex gap-4 items-center">
        <div className="flex w-full max-w-lg flex-col gap-4 itex-center justify-center rounded-large px-8 pb-10 pt-6">
          <div className="flex flex-col gap-4 items-center">
            <Image src="/img/logo-inverse.svg" width={100} height={80} alt="" />
            <p className="pb-4 text-left text-xl font-semibold">
              Regístrate. Es fácil y rápido
            </p>
          </div>
          <div className="text-2xl">
            <span>Referido por:</span>{" "}
            <span className="text-primary">{props.user.name}</span>
          </div>
          <form className="flex flex-col gap-4 " onSubmit={handleSubmit}>
            <Input
              isRequired
              label="Nombre y Apellidos"
              name="name"
              type="text"
              color="default"
              className=""
              
            />
            <Input
              isRequired
              label="Email"
              name="email"
              type="email"
              color="default"
            />
            {error.confirmEmail && (
              <span className="text-red-400 text-center">
                {t(error.confirmEmail as any)}
              </span>
            )}
            <Input
              isRequired
              label="Confirmar Email"
              name="confirmEmail"
              type="email"
              color="default"
            />
            <Input
              isRequired
              endContent={
                <div className="flex items-center">
                  <button type="button" onClick={toggleVisibility}>
                    {isVisible ? (
                      <FaRegEyeSlash className="pointer-events-none text-2xl text-default-400" />
                    ) : (
                      <FaRegEye className="pointer-events-none text-2xl text-default-400" />
                    )}
                  </button>
                </div>
              }
              label="Contraseña"
              name="password"
              type={isVisible ? "text" : "password"}
            />
            <Input
              isRequired
              endContent={
                <div className="flex items-center">
                  <button type="button" onClick={toggleConfirmVisibility}>
                    {isConfirmVisible ? (
                      <FaRegEyeSlash className="pointer-events-none text-2xl text-default-400" />
                    ) : (
                      <FaRegEye className="pointer-events-none text-2xl text-default-400" />
                    )}
                  </button>
                </div>
              }
              label="Confirma tu contraseña"
              name="confirmPassword"
              type={isConfirmVisible ? "text" : "password"}
            />
            {error.confirmPassword && (
              <span className="text-red-400 text-center">
                {t(error.confirmPassword as any)}
              </span>
            )}
            <div className="flex items-center">
              <Checkbox isRequired className="py-4" size="sm"></Checkbox>
              <span>
                Acepto los&nbsp;
                <Link href="/public/terms_es.pdf" size="sm" target="_blank">
                  Términos
                </Link>
                &nbsp;y&nbsp;
                <Link
                  href="/public/privacy_policy_es.pdf"
                  size="sm"
                  target="_blank"
                >
                  Política de Privacidad
                </Link>
              </span>
            </div>
            <Button color="primary" type="submit" size="lg" isLoading={loading}>
              Registrarse
            </Button>
            {error.network && (
              <span className="text-red-400 text-center">
                {t("errors_network")}
              </span>
            )}
          </form>
        </div>
      </div>
    </div>
  );
}
